<script>
export default {
  data() {
    return {
      menuOpen: false,
      showProducts: false,
      showSolutions: false,
      showBackground: false,
      isMobile: false,
      mouseX: 0,
      mouseY: 0,
      animatedElements: [],
      throttledScroll: null // 
    };
  },
  mounted() {
    // 

    // 
    this.initAnimations();

    this.checkScreenSize();
    window.addEventListener('resize', this.checkScreenSize);

    document.body.classList.add('page-loaded');
    document.addEventListener('mousemove', this.handleMouseMove);

    // 
    this.animatedElements = document.querySelectorAll('.scroll-animate');
    
    // 
    this.checkScroll();
    setTimeout(() => this.checkScroll(), 300);

    // 
    this.throttledScroll = this.throttle(this.checkScroll, 100);
    window.addEventListener('scroll', this.throttledScroll);

    // 
    setTimeout(() => {
      this.animatedElements.forEach(el => {
        if (this.isInViewport(el)) el.classList.add('in-view');
      });
    }, 500);
  },
  beforeDestroy() {
    // 
    window.removeEventListener('resize', this.checkScreenSize);
    document.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('scroll', this.throttledScroll); // 
  },
  methods: {

      // 
    initAnimations() {
      // 
      const fadeElements = document.querySelectorAll('.animate-fade-in');
      fadeElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(20px)';
        el.style.transition = 'opacity 0.8s ease, transform 0.8s ease';
      });
      
      // 
      setTimeout(() => {
        fadeElements.forEach(el => {
          const delay = el.dataset.delay || 0;
          setTimeout(() => {
            el.style.opacity = '1';
            el.style.transform = 'translateY(0)';
          }, delay);
        });
      }, 100);
    },

    checkScreenSize() {
      this.isMobile = window.innerWidth < 768;
      if (!this.isMobile) this.menuOpen = false;
    },

    // 2. 
    toggleMenu() {
      this.menuOpen = !this.menuOpen;
      this.showBackground = this.menuOpen;
    },

    // 3. /
    showSubmenu(type) {
      if (type === 'products') this.showProducts = true;
      else if (type === 'solutions') this.showSolutions = true;
    },
    hideSubmenu(type) {
      if (type === 'products') this.showProducts = false;
      else if (type === 'solutions') this.showSolutions = false;
    },

    // 4. 
    handleMouseMove(e) {
      const x = (e.clientX / window.innerWidth - 0.5) * 2;
      const y = (e.clientY / window.innerHeight - 0.5) * 2;
      this.mouseX = x;
      this.mouseY = y;
      this.applyParallaxEffect();
    },
    applyParallaxEffect() {
      const parallaxElements = document.querySelectorAll('.parallax');
      parallaxElements.forEach(el => {
        const speedX = parseFloat(el.getAttribute('data-speed-x')) || 0.1;
        const speedY = parseFloat(el.getAttribute('data-speed-y')) || 0.1;
        const translateX = this.mouseX * speedX * 50;
        const translateY = this.mouseY * speedY * 50;
        el.style.transform = `translate(${translateX}px, ${translateY}px)`;
      });
    },

    // 5. methods
    throttle(func, delay) {
      let lastTime = 0;
      return (...args) => {
        const now = Date.now();
        if (now - lastTime >= delay) {
          func.apply(this, args);
          lastTime = now;
        }
      };
    },
    isInViewport(el) {
      const rect = el.getBoundingClientRect();
      const viewHeight = window.innerHeight || document.documentElement.clientHeight;
      return rect.top < viewHeight && rect.bottom > 0; // 
    },
    checkScroll() {
      this.animatedElements.forEach(el => {
        const inView = this.isInViewport(el);
        const delay = el.dataset.delay || 0;
        if (inView && !el.classList.contains('in-view')) {
          el.style.transitionDelay = `${delay}ms`;
          el.classList.add('in-view');
        } else if (!inView && el.classList.contains('in-view')) {
          el.classList.remove('in-view');
        }
      });
    }
  }
};

document.addEventListener('DOMContentLoaded', () => {
    // 
    const years = [
        "202191",
        "2021929",
        "202268",
        "2022820",
        "2022831"
    ];
    
    let currentIndex = 0; // 
    let isAnimating = false;
    let autoPlayInterval; // 
    
    // DOM
    const yearElements = document.querySelectorAll('.year');
    const dotElements = document.querySelectorAll('.dot');
    const contentElements = document.querySelectorAll('.content-item');
    const timelineLine = document.querySelector('.timeline-line');

    // 
    if (!yearElements.length || !dotElements.length || !contentElements.length) {
        console.error('HTML');
        return;
    }

    // 
    function alignYearMarkers() {
        const lineWidth = timelineLine.offsetWidth;
        const markerContainer = document.querySelector('.year-markers');
        
        years.forEach((year, index) => {
            const marker = yearElements[index];
            const dot = dotElements[index];
            
            // 
            const dotRect = dot.getBoundingClientRect();
            const lineRect = timelineLine.getBoundingClientRect();
            const dotPosition = dotRect.left - lineRect.left + dotRect.width / 2;
            
            // 
            marker.style.position = 'absolute';
            marker.style.left = `${dotPosition - marker.offsetWidth / 2}px`;
        });
        
        // 
        markerContainer.style.position = 'relative';
        markerContainer.style.minHeight = '40px';
    }

    // 
    function updateActive(targetIndex) {
        if (isAnimating || targetIndex === currentIndex) return;
        isAnimating = true;

        // 
        const currentContent = contentElements[currentIndex];
        currentContent.style.transition = 'opacity 0.4s ease-out, transform 0.4s ease-out';
        currentContent.style.opacity = '0';
        currentContent.style.transform = 'translateY(20px)';
        
        setTimeout(() => {
            // active
            yearElements.forEach(el => el.classList.remove('active'));
            dotElements.forEach(el => el.classList.remove('active'));
            contentElements.forEach(el => el.classList.remove('active'));
            
            // active
            currentIndex = targetIndex;
            yearElements[currentIndex].classList.add('active');
            dotElements[currentIndex].classList.add('active');
            contentElements[currentIndex].classList.add('active');
            
            // 
            const targetContent = contentElements[currentIndex];
            targetContent.style.opacity = '0';
            targetContent.style.transform = 'translateY(20px)';
            targetContent.style.transition = 'opacity 0.4s ease-in, transform 0.4s ease-in';
            
            setTimeout(() => {
                targetContent.style.opacity = '1';
                targetContent.style.transform = 'translateY(0)';
                isAnimating = false;
            }, 50);
            
        }, 400);
    }

    // 
    function nextItem() {
        // 
        const nextIndex = currentIndex === years.length - 1 ? 0 : currentIndex + 1;
        updateActive(nextIndex);
    }

    // 3
    function startAutoPlay() {
        // 
        if (autoPlayInterval) clearInterval(autoPlayInterval);
        
        // 30003
        autoPlayInterval = setInterval(nextItem, 3000);
    }

    // 
    function setupHoverPause() {
        const timeline = document.querySelector('.timeline');
        timeline.addEventListener('mouseenter', () => {
            if (autoPlayInterval) clearInterval(autoPlayInterval);
        });
        
        timeline.addEventListener('mouseleave', () => {
            startAutoPlay();
        });
    }

    // 
    window.addEventListener('resize', alignYearMarkers);

    // 
    alignYearMarkers();
    updateActive(currentIndex);
    startAutoPlay(); // 
    setupHoverPause(); // 
});

tailwind.config = {
    theme: {
        extend: {
            colors: {
                primary: '#165DFF',
                secondary: '#00C48C',
                accent: '#FF7D00',
                dark: '#1D2939',
                light: '#F9FAFB',
                muted: '#667085'
            },
            fontFamily: {
                inter: ['Inter', 'system-ui', 'sans-serif'],
            },
            boxShadow: {
                'elevation-1': '0 1px 3px rgba(0,0,0,0.05), 0 1px 2px rgba(0,0,0,0.1)',
                'elevation-2': '0 4px 6px -1px rgba(0,0,0,0.1), 0 2px 4px -1px rgba(0,0,0,0.06)',
                'elevation-3': '0 10px 15px -3px rgba(0,0,0,0.1), 0 4px 6px -2px rgba(0,0,0,0.05)',
                'elevation-4': '0 20px 25px -5px rgba(0,0,0,0.1), 0 10px 10px -5px rgba(0,0,0,0.04)',
            }
        },
    }
}
</script>

