/*  */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: translateY(0);
    }
    to {
        opacity: 0;
        transform: translateY(30px);
    }
}

.scroll-animate {
    opacity: 0 !important;
    transform: translateY(30px) !important;
    transition: opacity 0.6s ease, transform 0.6s ease !important;
}
.scroll-animate.in-view {
    opacity: 1 !important;
    transform: translateY(0) !important;
}
            
