/**/
.timeline {
    width: 93%;
    margin: 50px 0 50px 0; /*  */
    padding: 40px 20px 60px;
    background-color: #a3cff52d;
    position: relative;
    font-family: 'Inter', sans-serif;
    color: #333;
    box-sizing: border-box;
    border-radius: 0 100px 100px 0;
}

.timeline-title {
    font-size: 26px;
    font-weight: 700;
    text-align: center;
    margin-bottom: 10px;
    line-height: 1.2;
}

.timeline-title:nth-of-type(2) {
    opacity: 0.5;
    margin-top: 0;
    margin-bottom: 60px;
}

.timeline-content {
    min-height: 80px;
    text-align: center;
    margin-bottom: 80px;
}

.content-item {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s ease, visibility 0.4s ease;
    position: absolute;
    width: 100%;
    transform: translateY(20px);

}

.content-item.active {
    opacity: 1;
    visibility: visible;
    position: relative;
    transform: translateY(0);
}

.content-item h3 {
    font-size: 1.25rem;
    font-weight: 600;
    margin-bottom: 8px;
    color: #1e40af;
}

.content-item p {
    font-size: 0.95rem;
    line-height: 1.6;
    color: #666;
    max-width: 800px;
    margin: 0 auto;
}

.timeline-nav {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    position: relative;
    width: 100%;
}

.year-markers {
    display: flex;
    justify-content: space-between;
    width: 90%;
    position: relative;
}

.year {
    font-size: 0.9rem;
    color: #999;
    cursor: pointer;
    transition: all 0.3s ease;
    white-space: nowrap;
    padding: 5px 0;
    position: relative;
    z-index: 2;
}

.year.active {
    color: #1e40af;
    font-weight: 600;
    font-size: 1rem;
}

.timeline-line-container {
    width: 80%;
    margin: 0 auto;
    position: relative;
}

.timeline-line {
    height: 2px;
    background: #e5e7eb;
    width: 90%;
    position: relative;
    margin-left: 70px;
    top: 0;
    left: 0;
}

.timeline-dots {
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: absolute;
    top: -6px;
    left: 0;
    z-index: 1;
}

.dot {
    width: 14px;
    height: 14px;
    background: #e5e7eb;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
}

.dot.active {
    background: #fff;
    border: 2px solid #3b82f6;
    transform: scale(1.2);
}

@media (max-width: 992px) {
    .year {
        font-size: 0.8rem;
    }
    .content-item h3 {
        font-size: 1.1rem;
    }
}

@media (max-width: 768px) {
    .timeline {
        padding: 30px 15px 50px;
    }
    .timeline-title {
        font-size: 22px;
    }
    .year-markers {
        width: 95%;
        gap: 5px;
    }
    .year {
        font-size: 0.75rem;
    }
    .timeline-content {
        margin-bottom: 60px;
    }
    .content-item p {
        font-size: 0.9rem;
        padding: 0 10px;
    }
}

@media (max-width: 576px) {
    .year-markers {
        flex-wrap: wrap;
        justify-content: center;
        gap: 15px;
    }
    .timeline-line-container {
        width: 100%;
    }
    .dot {
        width: 12px;
        height: 12px;
    }
}

