/**/
.Support-section {
    padding: 8rem 2rem;

    margin-top: 20px; /*  */
}

.Support-container {
    max-width: 1200px;
    margin: 0 auto;
}

.Support-Cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
    margin-top: 50px;
}

.Support-Card {
    background: white;
    border-radius: 15px;
    padding: 40px 30px;
    text-align: center;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.Support-Card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.1);
    background-image: linear-gradient(to top, #027df780, #ffffff);
}

.Support-Card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background: var(--primary);
    z-index: -1;
    transition: height 0.5s ease;
}

.Support-Card:hover::before {
    height: 100%;
}

.Support-Card-icon {
    width: 80px;
    height: 80px;
    background: rgba(0, 102, 204, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 25px;
    transition: all 0.3s;
}

.Support-Card-icon .icon {
    font-size: 2rem;
    color: var(--primary);
    transition: color 0.3s;
}

.Support-Card-Header {
    font-size: 1.5rem;
    margin-bottom: 15px;
    transition: color 0.3s;
}

.Support-Card-content {
    color: var(--gray);
    transition: color 0.3s;
}

.Support-Card:hover .Support-Card-icon {
    background: rgba(255, 255, 255, 0.2);
}

.Support-Card:hover .Support-Card-icon .icon {
    color: white;
}

.Support-Card:hover .Support-Card-Header {
    color: white;
}

.Support-Card:hover .Support-Card-content {
    color: rgba(255, 255, 255, 0.9);
}

@media (max-width: 768px) {
    .Support-section {
        padding: 5rem 1rem;
    }
    .Support-Card {
        flex: 1 1 100%;
        max-width: 100%;
        margin-bottom: 1.5rem;
    }
    .section-title {
        font-size: clamp(1.75rem, 5vw, 2.5rem);
    }
}

