}

/*  */
.menu-toggle {
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    padding: 10px;
    z-index: 101;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
}

.bg-main {
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: -1000;
}

/*  */
.position {
    width:75%;
    margin: 0 auto;
    padding: 40px 20px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 50px;
}
.Positionsection-header{
    margin-top: 70px;
}

/*  */
@media (min-width: 768px) {
    .position {
        flex-direction: row;
        align-items: flex-start;
    }
    
    .positionleft, .positionright, .position-center {
        width: calc(33.333% - 20px);
    }
}

/*  */
.positionleft, .positionright {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: -100px;
    gap: 10px;
}

/* / */
.positionleft-UP, .positionleft-Bottom, .positionright-UP, .positionright-Bottom {
    background-color: var(--bg-white);
    border-radius: 12px;
    padding: 24px;
    box-shadow: var(--shadow);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-left: 4px solid transparent;
}

/*  */
.positionleft-UP:hover, .positionleft-Bottom:hover, .positionright-UP:hover, .positionright-Bottom:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-hover);
}

/*  */
.positionIntroduction {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

/*  */
.positionIntroduction::before {
    content: "";
    width: 48px;
    height: 48px;
    background-color: var(--primary)/10;
    border-radius: 8px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: var(--primary);
}

/*  */
.positionleft-Bottom .positionIntroduction::before {
    background-color: var(--accent)/10;
    color: var(--accent);
}

/*  */
.positionright-UP .positionIntroduction::before {
    background-color: var(--secondary)/10;
    color: var(--secondary);
}

/*  */
.positionIntroductionHeader {
    font-size: 18px;
    font-weight: 600;
    color: var(--text-dark);
    margin-bottom: 8px;
}

/*  */
.positionIntroductionText {
    font-size: 14px;
    color: var(--text-medium);
    line-height: 1.6;
    margin-bottom: 12px;
}

/*  */
.positionIntroduction-a {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    color: var(--primary);
    transition: color 0.3s ease;
}

.positionIntroduction-a:hover {
    color: var(--primary)/80;
}

.positionIntroduction-a i {
    margin-left: 4px;
}

/*  */
.position-center {
    display: flex;
    justify-content: center;
    margin-top: 70px;
}

.position-center img {
    max-width: 100%;
    border-radius: 16px;
    transition: transform 0.5s ease;
}

.position-center img:hover {
    transform: scale(1.03);
}

/*  */
@media (max-width: 576px) {
    .position {
        padding: 20px 15px;
    }
    
    .positionleft-UP, .positionleft-Bottom,
    .positionright-UP, .positionright-Bottom {
        padding: 18px;
    }
    
    .positionIntroductionHeader {
        font-size: 16px;
    }
}

