<script>
export default {
  data() {
    return {
      // 
      isScrolling: false,
      lastScrollTime: 0,
      touchStartY: 0,
      isHover: false,
      scrollTimer: null,
      currentPage: 1,       // 
      totalPages: 4         // 
    }
  },
  mounted() {
    this.initCollisionAnimation();
    this.initAnimations();
    this.initEventListeners();
    // 
    this.updateCurrentPage();
  },
  beforeDestroy() {
    // 
    this.removeEventListeners();
  },
  methods: {
   // M
   initCollisionAnimation() {
      const loader = document.getElementById('loader');
      const mLeft = document.querySelector('.m-left');
      const mRight = document.querySelector('.m-right');
      const companyName = document.getElementById('company-name');
      const impactEffect = document.querySelector('.impact-effect');
      const logoContainer = document.querySelector('.logo-container');

      if (!loader || !mLeft || !mRight || !companyName || !impactEffect || !logoContainer) return;

      // 1. 
      mLeft.style.transform = 'translateX(-50px) translateY(-50%)';
      mRight.style.transform = 'translateX(50px) translateY(-50%)';
      // logo
      logoContainer.style.transform = 'translate(-50%, -50%) scale(0.8)';
      logoContainer.style.opacity = '0';
      
      // 2.  - 
      setTimeout(() => {
        mLeft.style.transition = 'transform 1.2s cubic-bezier(0.34, 1.56, 0.64, 1)';
        mRight.style.transition = 'transform 1.2s cubic-bezier(0.34, 1.56, 0.64, 1)';
        mLeft.style.transform = 'translateX(15px) translateY(-50%)';
        mRight.style.transform = 'translateX(-15px) translateY(-50%)';
        
        // 3.  - 
        setTimeout(() => {
          // 
          mLeft.style.transition = 'transform 0.3s ease-out, opacity 0.5s ease-out';
          mRight.style.transition = 'transform 0.3s ease-out, opacity 0.5s ease-out';
          mLeft.style.transform = 'translateX(20px) translateY(-50%) scale(1.1)';
          mRight.style.transform = 'translateX(-20px) translateY(-50%) scale(1.1)';
          
          //  - 
          impactEffect.style.transition = 'transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.8s ease-out';
          impactEffect.style.transform = 'translate(-50%, -50%) scale(8)';
          impactEffect.style.opacity = '0';
          
          // 4. logo - 
          setTimeout(() => {
            // M
            mLeft.style.transition = 'transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.6s ease-out';
            mRight.style.transition = 'transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94), opacity 0.6s ease-out';
            mLeft.style.transform = 'translateX(40px) translateY(-50%) scale(0.5)';
            mRight.style.transform = 'translateX(-40px) translateY(-50%) scale(0.5)';
            mLeft.style.opacity = '0';
            mRight.style.opacity = '0';
            
            // logo - ease-out
            setTimeout(() => {
              logoContainer.style.transition = 'all 1.2s cubic-bezier(0.25, 0.46, 0.45, 0.94)';
              logoContainer.style.opacity = '1';
              logoContainer.style.transform = 'translate(-50%, -50%) scale(1)';
              
              //  - 
              setTimeout(() => {
                companyName.style.transition = 'opacity 1.2s ease-out, transform 1.2s ease-out';
                companyName.style.opacity = '1';
                companyName.style.transform = 'translate(-50%, -20px)';
                
                //  - logo
                setTimeout(() => {
                  loader.style.transition = 'opacity 1s ease-out';
                  loader.style.opacity = '0';
                  setTimeout(() => {
                    loader.style.display = 'none';
                  }, 1000);
                }, 1500); // logo
              }, 500);
            }, 300); // Mlogo
          }, 300); // 
        }, 1200); // M
      }, 500);
    },
    
    // 
    updateCurrentPage() {
      const activePageEl = document.querySelector('.page-active');
      if (activePageEl) {
        this.currentPage = parseInt(activePageEl.dataset.page);
      }
    },
    
    // 
    scrollToPrevPage() {
      if (this.currentPage > 1) {
        this.scrollToSection(`page${this.currentPage - 1}`);
      }
    },
    
    // 
    scrollToNextPage() {
      if (this.currentPage < this.totalPages) {
        this.scrollToSection(`page${this.currentPage + 1}`);
      }
    },
    
    // 
    initEventListeners() {
      this.wheelHandler = this.handleWheel.bind(this);
      this.touchStartHandler = this.handleTouchStart.bind(this);
      this.touchEndHandler = this.handleTouchEnd.bind(this);
      this.mouseMoveHandler = this.handleMouseMove.bind(this);
      this.scrollHandler = this.handleScroll.bind(this);
      
      window.addEventListener('wheel', this.wheelHandler);
      window.addEventListener('touchstart', this.touchStartHandler, { passive: true });
      window.addEventListener('touchend', this.touchEndHandler, { passive: true });
      window.addEventListener('mousemove', this.mouseMoveHandler);
      window.addEventListener('scroll', this.scrollHandler);
    },
    
    // 
    removeEventListeners() {
      if (this.wheelHandler) window.removeEventListener('wheel', this.wheelHandler);
      if (this.touchStartHandler) window.removeEventListener('touchstart', this.touchStartHandler);
      if (this.touchEndHandler) window.removeEventListener('touchend', this.touchEndHandler);
      if (this.mouseMoveHandler) window.removeEventListener('mousemove', this.mouseMoveHandler);
      if (this.scrollHandler) window.removeEventListener('scroll', this.scrollHandler);
    },
    
    // 
    initAnimations() {
      const animatedElements = document.querySelectorAll('.animate-fade-in');
      
      animatedElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(20px)';
        const delay = parseInt(el.getAttribute('data-delay')) || 0;
        
        setTimeout(() => {
          el.style.transition = 'all 1000ms ease-out';
          el.style.opacity = '1';
          el.style.transform = 'translateY(0)';
        }, delay);
      });
    },
    
    // 
    handleWheel(e) {
      // 
      if (this.scrollTimer) {
        clearTimeout(this.scrollTimer);
      }
      
      // 
      e.preventDefault();
      
      const now = Date.now();
      const scrollCooldown = 800; 
      
      // 
      if (this.isScrolling || now - this.lastScrollTime < scrollCooldown) {
        return;
      }
      
      this.scrollTimer = setTimeout(() => {
        this.isScrolling = true;
        this.lastScrollTime = now;
        
        const activePageEl = document.querySelector('.page-active');
        if (!activePageEl) {
          this.isScrolling = false;
          return;
        }
        
        const currentPage = parseInt(activePageEl.dataset.page);
        const totalPages = document.querySelectorAll('.fullpage').length;
        const scrollThreshold = 30;
        
        // 
        if (e.deltaY < -scrollThreshold && currentPage > 1) {
          this.scrollToSection(`page${currentPage - 1}`);
        }
        // 
        else if (e.deltaY > scrollThreshold && currentPage < totalPages) {
          this.scrollToSection(`page${currentPage + 1}`);
        } else {
          this.isScrolling = false;
          return;
        }
        
        // 
        setTimeout(() => { 
          this.isScrolling = false; 
        }, scrollCooldown);
      }, 80);
      
      e.stopPropagation();
    },
    
    // 
    handleTouchStart(e) {
      this.touchStartY = e.touches[0].clientY;
    },
    
    // 
    handleTouchEnd(e) {
      const now = Date.now();
      const scrollCooldown = 800;
      
      if (!this.touchStartY || now - this.lastScrollTime < scrollCooldown) return;
      
      const activePageEl = document.querySelector('.page-active');
      if (!activePageEl) return;
      
      const touchEndY = e.changedTouches[0].clientY;
      const diff = this.touchStartY - touchEndY;
      const currentPage = parseInt(activePageEl.dataset.page);
      const totalPages = document.querySelectorAll('.fullpage').length;
      
      if (diff > 60 && currentPage < totalPages) {
        this.scrollToSection(`page${currentPage + 1}`);
        this.lastScrollTime = now;
      } else if (diff < -60 && currentPage > 1) {
        this.scrollToSection(`page${currentPage - 1}`);
        this.lastScrollTime = now;
      }
      
      this.touchStartY = 0;
    },
    
    // 
    handleMouseMove(e) {
      const x = (window.innerWidth - e.pageX) / 50;
      const y = (window.innerHeight - e.pageY) / 50;
      
      document.querySelectorAll('.parallax-element').forEach(el => {
        el.style.transform = `translate(${x}px, ${y}px) scale(1.05)`;
      });
    },
    
    // 
    handleScroll() {
      const header = document.querySelector('.wrap_head');
      if (header) {
        if (window.scrollY > 50) {
          header.classList.add('scrolled');
        } else {
          header.classList.remove('scrolled');
        }
      }
    },
    
    // 
    scrollToSection(sectionId) {
      const target = document.getElementById(sectionId);
      if (!target) {
        console.warn(`ID ${sectionId} `);
        return;
      }

      document.querySelectorAll('.fullpage').forEach(page => {
        page.classList.remove('page-active', 'page-enter', 'page-exit');
        page.classList.add('page-exit');
      });

      target.classList.remove('page-exit');
      target.classList.add('page-active');
      
      // 
      this.updateCurrentPage();

      setTimeout(() => {
        this.initAnimations();
      }, 500);

      this.closeMobileMenu();
    },
    
    // 
    cleanupBeforeRoute() {
      this.removeEventListeners();
      this.closeMobileMenu();
    },
    
    // 
    handleMobileRoute() {
      this.removeEventListeners();
      this.closeMobileMenu();
    },
    
    // 
    toggleMobileMenu() {
      const mobileMenu = document.getElementById('mobileMenu');
      const menuToggle = document.getElementById('menuToggle');
      
      if (mobileMenu && menuToggle) {
        mobileMenu.classList.add('open');
        menuToggle.classList.add('active');
        document.body.style.overflow = 'hidden';
      }
    },
    
    // 
    closeMobileMenu() {
      const mobileMenu = document.getElementById('mobileMenu');
      const menuToggle = document.getElementById('menuToggle');
      
      if (mobileMenu && menuToggle) {
        mobileMenu.classList.remove('open');
        menuToggle.classList.remove('active');
        document.body.style.overflow = '';
      }
    },
    
    // 
    handleMobileNav(sectionId) {
      this.scrollToSection(sectionId);
      this.closeMobileMenu();
    },
    
    // 
    hoverCard() {
      this.isHover = true;
    },
    
    // 
    unhoverCard() {
      this.isHover = false;
    }
  }
};
</script>

<style scoped>
/*  */
.loader {
  opacity: 1;
}

.m-left, .m-right {
