  opacity: 1;
  z-index: 10;
  transform: translateY(0) scale(1) rotateX(0);
}
.page-exit {
  opacity: 0;
  transform: translateY(-100px) scale(0.95) rotateX(-10deg);
}

/*  */
.animate-fade-in {
  opacity: 0;
  transition: all 1000ms ease-out;
}

.hover-lift {
  transition: transform 300ms;
}
.hover-lift:hover {
  transform: translateY(-4px);
}

.hover-scale {
  transition: transform 700ms ease-out;
}
.hover-scale:hover {
  transform: scale(1.05);
}

/*  */
.menu_item {
  position: relative;
  padding: 8px 16px;
  cursor: pointer;
}

.sub_list {
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 8px;
  width: 192px;
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: all 300ms;
  transform: translateY(-8px);
}

.sub_list li {
  padding: 8px 16px;
  transition: background-color 300ms;
}
.sub_list li:hover {
  background-color: rgba(22, 93, 255, 0.05);
}

.sub_list li a {
  display: block;
  width: 100%;
  color: #4B5563;
  transition: color 300ms;
}
.sub_list li a:hover {
  color: #165DFF;
}

.menu_item:hover .sub_list {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

/*  */
.mobile-menu {
  position: fixed;
  inset: 0;
  background-color: white;
  z-index: 50;
  transform: translateX(100%);
  transition: transform 500ms ease-in-out;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu.open {
  transform: translateX(0);
}

.bar {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #1D2939;
  margin: 6px 0;
  transition: all 300ms;
}

.menu-toggle.active .bar:nth-child(2) {
