/*  */
.card {
  background-color: rgba(255, 255, 255, 0.85);
  backdrop-filter: blur(12px);
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  transition: all 700ms;
}
.card:hover {
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.12);
