/*  */

.diagonal-section {
  position: relative;
  background-color: #2ebaef;
  overflow: hidden;
  /* 0 */
  margin-bottom: 0; 

  padding-bottom: 0;
}

.diagonal-section::before {
  content: '';
  position: absolute;
  top: -200px; 
  left: 0;
  width: 100%;
  height: 1000px; 
  background-color: #fff;
  /*  -26deg */
  transform: skewY(-26deg); 
  transform-origin: top left; 
  z-index: 1; 
}

.diagonal-section::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #fff;
  transform: skewY(-0deg);
  z-index: 1;
}





/* 2diagonal-section2 */
.diagonal-section2 {
  position: relative;
  background-color: #2ebaef;
  overflow: hidden;
  /* 01 */
  margin-top: 0; 
  padding-top: 0;
    margin-top: -100px;
}

.diagonal-section2::before {
  content: '';
  position: absolute;
  top: 0; 
  left: 0;
  width: 100%;
  /* 1 */
  height: 400px; 
  background-color: #fff;
  /* 1 26deg */
  transform: skewY(-28.45deg); 
  transform-origin: top left; 
  z-index: 1; 
}

.diagonal-section2::after {
  content: '';
  position: absolute;
  bottom: 150;
  left: 0;
  width: 100%;
  height: 100px;
  background-color: #fff;
  transform: skewY(-0deg);
  z-index: 1;
}



.diagonal-section.inverse {
  background-color: var(--primary);
  color: #fff;
}

.diagonal-section.inverse::before,
.diagonal-section.inverse::after {
  background-color: #2ebaef;
}

