<script>
export default {
  data() {
    return {
      menuOpen: false,
      showProducts: false,
      showSolutions: false,
      showBackground: false,
      isMobile: false,
      mouseX: 0,
      mouseY: 0,
      animatedElements: [],
      throttledScroll: null,
      
      // 
      currentIndex: 0,
      slideWidth: 0,
      slidesPerView: 0,
      interval: null,
      // 
      resizeHandler: null,
      mouseEnterHandler: null,
      mouseLeaveHandler: null,
      
      // 
      isDragging: false,
      startX: 0,
      scrollLeft: 0,
      currentTranslate: 0,
      prevTranslate: 0,
      animationID: null,
      isTransitioning: false,

       isPlaying: true, // 
      friendItems: []  // 
    };
  },
  mounted() {
    // 
    this.initAnimations();

    this.checkScreenSize();
    window.addEventListener('resize', this.checkScreenSize);

    document.body.classList.add('page-loaded');
    document.addEventListener('mousemove', this.handleMouseMove);

    // 
    this.animatedElements = document.querySelectorAll('.scroll-animate');
    
    // 
    this.checkScroll();
    setTimeout(() => this.checkScroll(), 300);

    // 
    this.throttledScroll = this.throttle(this.checkScroll, 100);
    window.addEventListener('scroll', this.throttledScroll);

    // 
    setTimeout(() => {
      this.animatedElements.forEach(el => {
        if (this.isInViewport(el)) el.classList.add('in-view');
      });
    }, 500);

    // 
    this.initCertificateCarousel();

     // 
  this.initFriendAnimation();




    this.friendItems.forEach(item => {
      item.removeEventListener('mouseenter', this.handleFriendMouseEnter);
      item.removeEventListener('mouseleave', this.handleFriendMouseLeave);
    });
  },
  beforeDestroy() {
    // 
    window.removeEventListener('resize', this.checkScreenSize);
    document.removeEventListener('mousemove', this.handleMouseMove);
    window.removeEventListener('scroll', this.throttledScroll);
    
    // 
    if (this.resizeHandler) {
      window.removeEventListener('resize', this.resizeHandler);
    }
    
    const container = document.querySelector('.certificate-container');
    if (container && this.mouseEnterHandler && this.mouseLeaveHandler) {
      container.removeEventListener('mouseenter', this.mouseEnterHandler);
      container.removeEventListener('mouseleave', this.mouseLeaveHandler);
    }
    
    // 
    this.clearAutoPlay();
    if (this.animationID) {
      cancelAnimationFrame(this.animationID);
    }
  },
  methods: {
        // 
    initFriendAnimation() {
      // 
      this.friendItems = document.querySelectorAll('.friendItem');
      
      // /
      const pauseBtn = document.getElementById('pauseBtn');
      const playBtn = document.getElementById('playBtn');
      
      if (pauseBtn) pauseBtn.addEventListener('click', this.handlePause);
      if (playBtn) playBtn.addEventListener('click', this.handlePlay);
      
      // 
      this.friendItems.forEach(item => {
        item.addEventListener('mouseenter', this.handleFriendMouseEnter);
        item.addEventListener('mouseleave', this.handleFriendMouseLeave);
      });
    },
    
    // 
    handlePause() {
      if (this.isPlaying) {
        this.friendItems.forEach(item => {
          item.style.animationPlayState = 'paused';
        });
        this.isPlaying = false;
      }
    },
    
    // 
    handlePlay() {
      if (!this.isPlaying) {
        this.friendItems.forEach(item => {
          item.style.animationPlayState = 'running';
        });
        this.isPlaying = true;
      }
    },
    
    // 
    handleFriendMouseEnter(e) {
      e.currentTarget.style.animationPlayState = 'paused';
    },
    
    // 
    handleFriendMouseLeave(e) {
      if (this.isPlaying) {
        e.currentTarget.style.animationPlayState = 'running';
      }
    },

    // 
    initAnimations() {
      // 
      const fadeElements = document.querySelectorAll('.animate-fade-in');
      fadeElements.forEach(el => {
        el.style.opacity = '0';
        el.style.transform = 'translateY(20px)';
        el.style.transition = 'opacity 0.8s ease, transform 0.8s ease';
      });
      
      // 
      setTimeout(() => {
        fadeElements.forEach(el => {
          const delay = el.dataset.delay || 0;
          setTimeout(() => {
            el.style.opacity = '1';
            el.style.transform = 'translateY(0)';
          }, delay);
        });
      }, 100);
    },

    checkScreenSize() {
      this.isMobile = window.innerWidth < 768;
      if (!this.isMobile) this.menuOpen = false;
      
      // 
      if (this.slideWidth) {
        this.updateCarouselConfig();
      }
    },

    // 
    toggleMenu() {
      this.menuOpen = !this.menuOpen;
      this.showBackground = this.menuOpen;
    },

    // /
    showSubmenu(type) {
      if (type === 'products') this.showProducts = true;
      else if (type === 'solutions') this.showSolutions = true;
    },
    hideSubmenu(type) {
      if (type === 'products') this.showProducts = false;
      else if (type === 'solutions') this.showSolutions = false;
    },

    // 
    handleMouseMove(e) {
      const x = (e.clientX / window.innerWidth - 0.5) * 2;
      const y = (e.clientY / window.innerHeight - 0.5) * 2;
      this.mouseX = x;
      this.mouseY = y;
      this.applyParallaxEffect();
    },
    applyParallaxEffect() {
      const parallaxElements = document.querySelectorAll('.parallax');
      parallaxElements.forEach(el => {
        const speedX = parseFloat(el.getAttribute('data-speed-x')) || 0.1;
        const speedY = parseFloat(el.getAttribute('data-speed-y')) || 0.1;
        const translateX = this.mouseX * speedX * 50;
        const translateY = this.mouseY * speedY * 50;
        el.style.transform = `translate(${translateX}px, ${translateY}px)`;
      });
    },

    // 
    throttle(func, delay) {
      let lastTime = 0;
      return (...args) => {
        const now = Date.now();
        if (now - lastTime >= delay) {
          func.apply(this, args);
          lastTime = now;
        }
      };
    },
    isInViewport(el) {
      const rect = el.getBoundingClientRect();
      const viewHeight = window.innerHeight || document.documentElement.clientHeight;
      return rect.top < viewHeight && rect.bottom > 0; // 
    },
    checkScroll() {
      this.animatedElements.forEach(el => {
        const inView = this.isInViewport(el);
        const delay = el.dataset.delay || 0;
        if (inView && !el.classList.contains('in-view')) {
          el.style.transitionDelay = `${delay}ms`;
          el.classList.add('in-view');
        } else if (!inView && el.classList.contains('in-view')) {
          el.classList.remove('in-view');
        }
      });
    },

    // 
    initCertificateCarousel() {
      // DOM
      this.$nextTick(() => {
        const track = this.$refs.certificateTrack;
        if (!track) {
          console.warn('');
          return;
        }
        
        const slides = track.querySelectorAll('.certificate-slide');
        if (slides.length === 0) {
          console.warn('');
          return;
        }
        
        // 
        this.slideWidth = slides[0].offsetWidth;
        this.slidesPerView = window.innerWidth >= 1024 ? 3 : 2;
        
        // 
        this.createDots();
        
        // 
        this.startAutoPlay();
        
        // 
        const container = document.querySelector('.certificate-container');
        if (container) {
          // 
          this.mouseEnterHandler = () => this.clearAutoPlay();
          this.mouseLeaveHandler = () => this.startAutoPlay();
          
          container.addEventListener('mouseenter', this.mouseEnterHandler);
          container.addEventListener('mouseleave', this.mouseLeaveHandler);
        }
        
        // 
        this.resizeHandler = () => this.updateCarouselConfig();
        window.addEventListener('resize', this.resizeHandler);
      });

    },
    
    // 
    updateCarouselConfig() {
      const track = this.$refs.certificateTrack;
      if (!track) return;
      
      const slides = track.querySelectorAll('.certificate-slide');
      if (slides.length === 0) return;
      
      this.slideWidth = slides[0].offsetWidth;
      this.slidesPerView = window.innerWidth >= 1024 ? 3 : 2;
      this.currentIndex = 0;
      this.currentTranslate = 0;
      this.prevTranslate = 0;
      this.goToPage(0);
      this.createDots();
    },
    
    // 
    createDots() {
      const dotsContainer = this.$refs.certificateDots;
      if (!dotsContainer) return;
      
      dotsContainer.innerHTML = '';
      const track = this.$refs.certificateTrack;
      if (!track) return;
      
      const slides = track.querySelectorAll('.certificate-slide');
      if (slides.length === 0) return;
      
      const totalPages = Math.ceil(slides.length / this.slidesPerView);
      
      for (let i = 0; i < totalPages; i++) {
        const dot = document.createElement('div');
        dot.className = `certificate-dot ${i === this.currentIndex ? 'active' : ''}`;
        dot.addEventListener('click', () => this.goToPage(i));
        dotsContainer.appendChild(dot);
      }
    },
    
    // 
    goToPage(index) {
      const track = this.$refs.certificateTrack;
      if (!track) return;
      
      const slides = track.querySelectorAll('.certificate-slide');
      if (slides.length === 0) return;
      
      this.currentIndex = index;
      const maxIndex = Math.ceil(slides.length / this.slidesPerView) - 1;
      
      // 
      if (this.currentIndex > maxIndex) this.currentIndex = maxIndex;
      if (this.currentIndex < 0) this.currentIndex = 0;
      
      // 
      this.currentTranslate = -this.currentIndex * this.slideWidth * this.slidesPerView;
      this.prevTranslate = this.currentTranslate;
      
      // 
      track.style.transform = `translateX(${this.currentTranslate}px)`;
      track.style.transition = 'transform 0.5s ease';
      
      // 
      this.updateDots();
      
      // 
      this.resetAutoPlay();
    },
    
    // 
    updateDots() {
      const dotsContainer = this.$refs.certificateDots;
      if (!dotsContainer) return;
      
      const dots = dotsContainer.querySelectorAll('.certificate-dot');
      dots.forEach((dot, i) => {
        dot.classList.toggle('active', i === this.currentIndex);
      });
    },
    
    // 
    startAutoPlay() {
      this.clearAutoPlay();
      this.interval = setInterval(() => {
        const track = this.$refs.certificateTrack;
        if (!track) {
          this.clearAutoPlay();
          return;
        }
        
        const slides = track.querySelectorAll('.certificate-slide');
        if (slides.length === 0) {
          this.clearAutoPlay();
          return;
        }
        
        const maxIndex = Math.ceil(slides.length / this.slidesPerView) - 1;
        this.currentIndex = this.currentIndex >= maxIndex ? 0 : this.currentIndex + 1;
        this.goToPage(this.currentIndex);
      }, 5000); // 5
    },
    
    // 
    clearAutoPlay() {
      if (this.interval) {
        clearInterval(this.interval);
        this.interval = null;
      }
    },
    
    // 
    resetAutoPlay() {
      this.clearAutoPlay();
      this.startAutoPlay();
    },
    
    // 
    startDrag(e) {
      // 
      this.clearAutoPlay();
      
      // 
      this.isDragging = true;
      
      // 
      this.startX = e.type.includes('mouse') ? e.pageX : e.touches[0].pageX;
      
      // 
      const track = this.$refs.certificateTrack;
      if (track) {
        // 
        track.style.transition = 'none';
        this.prevTranslate = this.currentTranslate;
      }
    },
    
    handleDrag(e) {
      if (!this.isDragging) return;
      
      // 
      e.preventDefault();
      
      const track = this.$refs.certificateTrack;
      if (!track) return;
      
      // 
      const currentX = e.type.includes('mouse') ? e.pageX : e.touches[0].pageX;
      
      // 
      const diffX = currentX - this.startX;
      
      // 
      this.currentTranslate = this.prevTranslate + diffX;
      
      // 
      track.style.transform = `translateX(${this.currentTranslate}px)`;
    },
    
    endDrag() {
      if (!this.isDragging) return;
      
      // 
      this.isDragging = false;
      
      const track = this.$refs.certificateTrack;
      if (!track) return;
      
      // 
      track.style.transition = 'transform 0.5s ease';
      
      // 
      const slides = track.querySelectorAll('.certificate-slide');
      if (slides.length === 0) return;
      
      // 
      const movedDistance = this.currentTranslate - this.prevTranslate;
      const slideWidth = this.slideWidth * this.slidesPerView;
      const moveRatio = Math.abs(movedDistance) / slideWidth;
      
      // 
      const totalPages = Math.ceil(slides.length / this.slidesPerView);
      
      // /
      if (moveRatio > 0.3) {
        if (movedDistance < 0) {
          // 
          this.currentIndex = Math.max(0, this.currentIndex - 1);
        } else {
          // 
          this.currentIndex = Math.min(totalPages - 1, this.currentIndex + 1);
        }
      }
      
      // 
      this.goToPage(this.currentIndex);
      
      // 
      this.startAutoPlay();
    }
  },

  
};
</script>

