  flex-wrap: wrap;
  gap: 40px;
}

.contact-form {
  flex: 1;
  min-width: 300px;
  background-color: var(--light);
  border-radius: var(--radius);
  padding: 30px;
  box-shadow: var(--shadow);
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
